.sidebar {
	background: linear-gradient(225deg, #33BD7D 0%, #008F8D 100%);
	display: flex;
	/*align-items: center;*/
	flex-direction: column;
	padding: 50px 0;	
	text-align: center;
	color: #fff;
	position: fixed;
	top: 0;
	left: 40%;
	bottom: 0;
	transform: translateX(-100%);
}

.appTitle {
	font-size: 30px;
	margin: 0;
	font-weight: 500;
}

.img {
	display: block;
	width: 73%;
	margin: auto;
}

.img img {
	width: 100%;
	height: 100%;
}

@media (max-width: 768px) {
  .sidebar {
    position: relative;
    transform: initial;
    left: 0;
  }
}

@media (max-width: 480px) {
  .appTitle {
  	font-size: 24px;
  }	
}
